import React from 'react';
import useMediaQuery from 'react-responsive';
import style from './style.module.css';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import LinkHolder from '../LinkHolder/LinkHolder';
import StarIcon from '@material-ui/icons/Star';
import TelegramIcon from '@material-ui/icons/Telegram';
import GitHubIcon from '@material-ui/icons/GitHub';
import AlternateEmailIcon from '@material-ui/icons/AlternateEmail';
import WorkIcon from '@material-ui/icons/Work';
import { createMuiTheme, withStyles, makeStyles, ThemeProvider } from '@material-ui/core/styles';
import Backdrop from '@material-ui/core/Backdrop';
import Pdf from './resume.pdf';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    '& > *': {
      margin: theme.spacing(1),
    },
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  },
}));







const ArrowDownStyle = {
    color: '#16519F',
    fontSize: '80px'
}



const theme = createMuiTheme({
  color: '#16519F'
});



const FirstPage = (props) => {


  const [open, setOpen] = React.useState(false);
  const handleClose = () => {
    //alert("чпокчпокчпок");
  };
  const handleToggle = () => {
    setOpen(!open);
  };

  const classes = useStyles();

  const SimpleBackdrop = (props) =>{
    return (
      <div onClick = {() => props.handleClose}>
        <Backdrop className={classes.backdrop} open={props.open}  onClick = {() => props.handleClose}>
          <LinkHolder  onClick = {() => props.handleClose}/>
        </Backdrop>
      </div>
    );
  }
  
  
    const ButtonStyle = {
    color: '#16519F',
    textTransform: 'none',
    fontFamily: 'Roboto Mono'
  }

  if (open === false){

  return (
      <div className = {style.wrapper}>
              <div className = {style.arrowDown}>
                <a href = '#secondPage' style = {{textDecoration: "none"}}>
                  <ExpandMoreIcon style = {ArrowDownStyle} />
                </a>
              </div>
              <div className = {style.content}>
                <h2 className = {style.greetings}>Привет, меня зовут Никита 👋</h2>
                <h2 className = {style.description}>Я создаю web-приложения. Ищу применение своим навыкам.</h2>
                <div className = {style.buttonWrapper}>
                      <Button startIcon={<StarIcon/>} style = {ButtonStyle}><h2 className = {style.linkName}><a href="#secondPage" className = {style.aRef}>Навыки</a></h2></Button>
                      <Button startIcon={<TelegramIcon/>} style = {ButtonStyle}><h2 className = {style.linkName}><a href="https://t.me/Zhihorka" className = {style.aRef}>Телеграм</a></h2></Button>
                      <Button startIcon={<GitHubIcon/>} style = {ButtonStyle}><h2 className = {style.linkName}><a href="https://github.com/Zhihorka" className = {style.aRef}>GitHub</a></h2></Button>
                      <Button startIcon={<AlternateEmailIcon/>} style = {ButtonStyle}><h2 className = {style.linkName}><a href="#"  className = {style.aRef} onClick = {handleToggle}>Почта</a></h2></Button>
                      <Button startIcon={<WorkIcon/>} style = {ButtonStyle}><h2 className = {style.linkName}><a href = {Pdf} target = "_blank" className = {style.aRef} rel = "noreferrer">Резюме</a></h2></Button>
                </div>

              </div>
            
      </div>
    );
  }else if (open === true){
    return(
    <div className = {style.wrapper}>
      <div className = {style.arrowDown}>
        <a href = '#secondPage' style = {{textDecoration: "none"}}>
          <ExpandMoreIcon style = {ArrowDownStyle} />
        </a>
      </div>
      <div className = {style.content}>
      

        </div>
        <SimpleBackdrop className={classes.backdrop} open={open} handleClose={handleClose}/>
    </div>
    );
  }
}

export default FirstPage;